import { getAuth } from "@clerk/remix/ssr.server";
import { json, LoaderArgs, redirect } from "@remix-run/node";
import { useLoaderData } from "@remix-run/react";
import { useEffect, useState } from "react";
import DashHeader from "~/components/dash-header";
import { ApiKey, getApiKeyByUser } from "~/db/apikey.server";

export async function loader({ request }: LoaderArgs) {
    const { userId } = await getAuth(request);
    
    if(!userId) {
        return redirect("/sign-up");
    }
    const apiKey = await getApiKeyByUser(userId) as ApiKey;
    return json({ apiKey });
}

export default function DashboardDeveloperRoute() {
    
    const { apiKey } = useLoaderData();
    const [baseUrl, setBaseUrl] = useState("");
    const [showApiKey, setShowApiKey] = useState(false);
    const [enpointUrl, setEndpointUrl] = useState("");

    useEffect(() => {
        setBaseUrl(window.location.origin);
    }, []);

    useEffect(() => {
        setEndpointUrl(`${baseUrl}/api/tips?api_key=${showApiKey ? apiKey : "••••••••••••"}`);
    }, [baseUrl, showApiKey]);

    return (
        <div>
            <DashHeader title="Developer" />
            <p className="mt-4 font-light">To use the api rest of the tip of the day, send the value of your Api Key as a query parameter in the request</p>
            <div className="overflow-x-auto relative mt-6">
                <table className="w-full text-sm text-left text-gray-500 dark:text-gray-400">
                    <thead className="text-xs text-gray-700 uppercase bg-gray-50">
                        <tr>
                            <th scope="col" className="py-3 px-6">
                                Api Key
                            </th>
                            <th scope="col" className="py-3 px-6">
                                Actions
                            </th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr className="bg-white border-b">
                            <th scope="row" className="py-4 px-6 font-medium text-gray-900 whitespace-nowrap">
                                <p>{ apiKey?.value }</p>
                            </th>
                            <td className="py-4 px-6">
                                <div className="inline-flex gap-2 text-black">
                                    <button onClick={() => {}} className="underline hover:opacity-80">View</button>
                                    <button onClick={() => {}} className="hover:opacity-80">Refresh</button>
                                </div>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
            <h2 className="font-bold mt-6 text-xl">Endpoint</h2>
            <p className="font-light mt-2">Usage example</p>
            <div className="bg-gray-100 p-2 mt-2 rounded">
                <a className="font-mono text-sm underline" href={enpointUrl} target="_blank">{enpointUrl}</a>
            </div>
        </div>
    );
}